.navigation {
    position: fixed;
    top: 0; left: 0; bottom: 0;  right: 0;
    background: rgba(white, 0.9);
    /* display: flex; */
    align-items: center;
    justify-content: center;
    display: none;
}

.navigation ul {
    padding: 0;
    margin: 0;
}

.navigation li {
    display: block;
   list-style: none outside;
   padding: 1em 0;
   animation: navtoggle 1s 1 ease forwards;
   opacity: 0;
   transform: scale(0.1) translate(0, 3rem) rotate(720deg);
}

@for $i from 1 to 6 {
    .navigation li:nth-child(#{$i}) {
        animation-delay: $i * 0.15s;
    }
}


.navigation a {
    display: block;
    padding: 1.5em 2.5em;
    background: white;
    text-decoration: none;
    color: #444;
    box-shadow: 0 0 1.5rem 0.1rem rgba(0, 0, 0, 0.2);
    border-radius: 0.5rem;
    font-size: 1.5em;
  
}

.navigation a::before {
    margin-right: 1em;
}

#nav-main-toggle {
    display: none;
}

#nav-main-toggle:checked + .site-header .navigation {
    display: flex;
    z-index: 1000;
}

.nav-toggle {
    position: fixed;
    top: 2rem; left: 2rem;
    z-index: 1000;
    font-size: 2rem;
    padding: 0 0.5rem;
    animation: navitemin 0.5s 1 ease-in-out forwards;
}

#nav-toggle-left {

}

#nav-toggle-right {
    left: inherit;
    right: 2rem;
}

.nav-toggle span {
    display: none
}

.nav-toggle::before {
  margin-right: 1rem;
}

.nav-toggle::before {
    content: '\f0c9';
}

#nav-main-toggle:checked + .site-header .nav-toggle::before {
    content: '\f00d';
}

#nav-main-toggle:checked + .site-header .nav-toggle {
    z-index: 1010;
    animation: jump 0.3s 1 ease-in-out forwards;
}

